

import Vue from 'vue'
import App from './App'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'


import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload, {
error: require('../../assets/loading.png'),//这个是请求失败后显示的图片
loading: require('../../assets/loading.png'),//这个是加载的loading过渡效果
try: 1, // 这个是加载图片数量,
adapter: {
loaded ({el, naturalHeight, naturalWidth, $parent, src, loading, error, Init}) {

$(el).width(naturalWidth/2)
},
}
})
Vue.use(AlertPlugin)
Vue.use(ToastPlugin)



Vue.config.productionTip = false

/* eslint-disable no-new */

new Vue({
el: '#app',

template: '<App/>',
components: { App }
})




